        background-position: center;
        background-repeat: no-repeat;
        background-color: transparent; }
        .tesla_counter .tesla_counter_wrapper .tesla_counter_arrow button:disabled {
          opacity: 0.4;
          cursor: not-allowed; }
        .tesla_counter .tesla_counter_wrapper .tesla_counter_arrow button:first-child {
          border-bottom: 1px solid #fff;
          background-image: url("../assets/images/counter/up.svg"); }
        .tesla_counter .tesla_counter_wrapper .tesla_counter_arrow button:last-child {
          border-top: 1px solid #ccc;
          background-image: url("../assets/images/counter/down.svg"); }



